
import { NLayoutSider } from 'naive-ui';
import { defineComponent, h, ref } from 'vue';
import { RouterLink } from 'vue-router';
import { darkTheme } from 'naive-ui';

const menuOptions = [
  {
    label: () =>
      h(
        RouterLink,
        {
          to: {
            path: '/cms',
          },
        },
        { default: () => 'info' },
      ),
    key: 'info',
    icon: () => h('i', { class: 'iconfont icon-info' }),
  },
  {
    label: () =>
      h(
        RouterLink,
        {
          to: {
            path: '/cms/blogs',
          },
        },
        { default: () => 'blogs' },
      ),
    key: 'blogs',
    icon: () => h('i', { class: 'iconfont icon-blog' }),
  },
  {
    label: () =>
      h(
        RouterLink,
        {
          to: {
            path: '/cms/barrages',
          },
        },
        { default: () => 'barrages' },
      ),
    key: 'barrages',
    icon: () => h('i', { class: 'iconfont icon-barrage-l' }),
  },
  {
    label: () =>
      h(
        RouterLink,
        {
          to: {
            path: '/cms/imgs',
          },
        },
        { default: () => 'imgs' },
      ),
    key: 'imgs',
    icon: () => h('i', { class: 'iconfont icon-tupian' }),
  },
  {
    label: () =>
      h(
        RouterLink,
        {
          to: {
            path: '/cms/links',
          },
        },
        { default: () => 'links' },
      ),
    key: 'links',
    icon: () => h('i', { class: 'iconfont icon-lianjie' }),
  },
];

export default defineComponent({
  setup() {
    let collapsed = ref<boolean>(false);
    const side = ref<typeof NLayoutSider | null>(null);
    const pullinout = () => {
      collapsed.value = !collapsed.value;
      if (!side.value) return;
      side.value.style = 'width: 120px';
    };

    return {
      menuOptions,
      darkTheme,
      collapsed,
      pullinout,
      side,
    };
  },
});
